.content {
  margin: 0 auto;
  padding: 0 15px;
  @media (max-width: 599px) {
    column-count: 1;
    column-gap: 10px;
  }
  @media (min-width: 600px) and (max-width: 949px) {
    column-count: 2;
    column-gap: 10px;
  }
  @media (min-width: 950px) and (max-width: 1099px) {
    column-count: 3;
    column-gap: 10px;
  }
  @media (min-width: 1100px) {
    column-count: 4;
    column-gap: 10px;
  }
}
.contentFlex {
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  @media (max-width: 599px) {
    column-count: 1;
    column-gap: 10px;
  }
  @media (min-width: 600px) and (max-width: 949px) {
    column-count: 2;
    column-gap: 10px;
  }
  @media (min-width: 950px) and (max-width: 1099px) {
    column-count: 3;
    column-gap: 10px;
  }
  @media (min-width: 1100px) {
  }
}
.divider {
  border-bottom: 1px solid grey;
  height: 2px;
  width: 80%;
  margin: {
    top: 20px;
    bottom: 10px;
    left: auto;
    right: auto;
  }
}
